import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Edificio } from '../../ubicacion/interfaces/Ubicacion';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ServiceEdificioService {

  url = `${environment.urlG}/edificio`
  constructor(private http:HttpClient) { }

  public edificios(): Observable<any> {
    return this.http.get<any>(`${this.url}`)
  }
  guardar(data: Edificio){
    return this.http.post(`${this.url}`,data)
  }
  editar(data: Edificio){
    return this.http.put(`${this.url}/${data.id}`,data)
  }
  eliminar(data: Edificio){
    return this.http.delete(`${this.url}/${data.id}`)
  }
}
