import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { Edificio } from 'src/app/pages/ubicacion/interfaces/Ubicacion';
import { ServiceEdificioService } from '../../service/service-edificio.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-listar-edificio',
  templateUrl: './listar-edificio.component.html',
  styleUrls: ['./listar-edificio.component.scss']
})
export class ListarEdificioComponent implements OnInit {

  breadCrumbItems: Array<{}>;
  edificios:Edificio[]=[]
  edificioSeleccionado!:Edificio
  formulario!: FormGroup
  butttonText:string
    /**DataTables */
    @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
    dtOptions: DataTables.Settings = {};
    dtTrigger: Subject<Edificio> = new Subject<Edificio>();

  constructor(private edificioServicio: ServiceEdificioService, private modalService: NgbModal, private fb: FormBuilder) {
    this.formulario=this.initFomulario();
   }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Edificio' }, { label: 'Listado', active: true }];
    this.initFomulario();
    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2, 3] },
        { orderable: false, targets: [3] },
        { searchable: false, targets: [3] },
/*         { width: "35%", targets: [1,3] },
        { width: "10%", targets: [0, 2, 4] }, */
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatos();

  }

  ngOnDestroy() {
    this.dtTrigger.unsubscribe()
    // this.dtTrigger.complete()
  }

  openModal(content: any,reset:boolean) {
    if(reset){
      this.edificioSeleccionado=null;
      this.butttonText="Guardar";
      this.formulario.reset()
      this.modalService.open(content);
    }else{
      this.butttonText="Editar";     
      this.modalService.open(content); 
    }   
  }

  private initFomulario(): FormGroup {
    return this.fb.group({
      edificio: ['', [Validators.required, Validators.minLength(4)]],
      niveles: ['', [Validators.required, Validators.min(1)]], 
    })
  }

  cargarDatos() {
    this.edificioServicio.edificios().subscribe((resp: any) => {
      this.edificios = resp;
      this.dtTrigger.next(null)
    });
  }


  eliminar(edifcio: Edificio) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: 'Sí, borrar',
      cancelButtonText:'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        this.edificioServicio.eliminar(edifcio).subscribe(resp => {
          Swal.fire('Eliminado', 'El registro ha sido eliminado', 'success');
          this.reload()
        },
          (err: any) => {
            Swal.fire({
              icon: 'error',
              title: 'Error',
              text: err.error.msg
            })
          });
      } else if (result.isDenied) {
        Swal.fire('Cambios no aplicados', '', 'info')
      }
    });
  }

  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      this.edificios = []
      dtInstance.destroy()
      this.cargarDatos()
    })
  }

  reset() {
    this.formulario.reset()
  }

  editarlo(seleccion:Edificio, modal:any){
    this.edificioSeleccionado=seleccion;
    this.openModal(modal,false);
  }

  accion(){
    if(this.edificioSeleccionado!=null){
      this.editar();
    }else{
      this.guardar();
    }
  }


  guardar() {
    const edificio: Edificio=this.formulario.value
    this.edificioServicio.guardar(edificio).subscribe((resp: any) => { 
      if (resp.mensaje === "Error edificio existente") {
        Swal.fire('Registro existente', '', 'warning');
      } else {
        if (resp) {
          Swal.fire('Éxito', 'Almacenado correctamente', 'success');
          this.reload()
          this.modalService.dismissAll()
          this.formulario.reset()
        } else {
          Swal.fire('Ocurrio un problema', '', 'warning');
        }        
      }    
      
    })
    return Object.values(this.formulario.controls)
      .forEach(control =>
        control.markAsTouched())
  }


  editar() {
   
    this.edificioSeleccionado.edificio=this.formulario.controls['edificio'].value
    this.edificioSeleccionado.niveles=this.formulario.controls['niveles'].value
    this.edificioServicio.editar(this.edificioSeleccionado).subscribe(resp => {
      Swal.fire('Éxito', 'Modificado correctamente', 'success');
      this.modalService.dismissAll();
      this.edificioSeleccionado=null;
      this.reset();
      this.reload();
    }, (err) => {
      Swal.fire({
        title: 'Error',
        text: `Algo fallo`,
        icon: 'error',
      });
      this.reload();
    })
  }


  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }

  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg'});
  }


}
